.popup__container-image {
    margin: 0;
    width: auto;
    aspect-ratio: auto;
    background-color: transparent;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  
  @media screen and (max-width: 424px) {
    .popup__container-image {
      margin: 0 40px;
    }
  }